.site-header {
  position: absolute;
  width: 100%;
  padding: 25px 0;
  z-index: 3;
}
.site-header__wrapper {
  display: flex;
  justify-content: space-between;
}
.site-header__content {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.site-header__logo {
  min-width: 80px;
}
.site-header__nav {
  transition: all 0.3s ease;
}
@media screen and (max-width: 1200px) {
  .site-header__nav {
    position: fixed;
    left: 0;
    top: 0;
    height: 100vh;
    width: 100%;
    transform: translateX(-200%);
    background-color: #050835;
  }
  .site-header__nav .site-header__ul {
    padding-top: 20%;
    flex-direction: column;
    align-items: center;
  }
  .site-header__nav .site-header__ul a {
    color: #fff;
  }
}
.site-header__ul {
  display: flex;
  justify-content: space-between;
}
.site-header__ul a {
  padding: 10px 14px;
  font-weight: 500;
  color: #2d2e33;
}
.site-header__form-wrapper {
  padding: 0 30px;
  margin: 0 30px 0 15px;
  border-left: 1px solid #cbccce;
  border-right: 1px solid #cbccce;
}
.site-header__form {
  padding: 5px 20px 5px 5px;
  border-radius: 50px;
  background-color: #fff;
}
@media screen and (max-width: 576px) {
  .site-header__form {
    display: none;
  }
}
@media screen and (max-width: 1360px) {
  .site-header__form {
    padding: 0;
  }
  .site-header__form span {
    display: none;
  }
}
.site-header__btn {
  padding: 10.5px 31px;
  color: #fff;
  background-color: #050835;
}
.site-header__language-switcher {
  font-weight: 500;
  border: 0;
  text-transform: uppercase;
  color: #2d2e33;
  background-color: transparent;
}

.show-menu {
  transform: translateX(0);
}

