}

.bar1, .bar2, .bar3 {
  width: 25px;
  height: 4px;
  background-color: #000;
  margin: 2px 0;
  transition: 0.4s;
  border-radius: 20px;
}

.change .bar1 {
  transform: rotate(-45deg) translate(0, 1px);
  background-color: #fff;
}

.change .bar2 {
  opacity: 0;
}

.change .bar3 {
  transform: rotate(45deg) translate(-7px, -8px);
  background-color: #fff;
}

.hero {
  position: relative;
  padding: 200px 0;
  text-align: center;
  color: #fff;
  background-image: url("../../../assets/img/hero.png");
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
}
.hero::after {
  content: "";
  position: absolute;
  width: 100%;
  height: 100%;
  left: 0;
  top: 0;
  mix-blend-mode: multiply;
  background: transparent -webkit-gradient(linear, left top, left bottom, from(#ffd2ac), to(rgba(144, 70, 7, 0.8666666667))) 0% 0% no-repeat padding-box;
}
